import kotlinx.css.minWidth
import kotlinx.css.padding
import kotlinx.css.px
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv

external interface NavigationProps: RProps {
    var onShowAnswerClick: () -> Unit
    var onNextClick: () -> Unit
}

class Navigation : RComponent<NavigationProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("d-flex flex-wrap justify-content-around")
                padding(vertical = 20.px)
            }
            styledButton {
                css {
                    classes = mutableListOf("btn btn-danger")
                    minWidth = 150.px
                }
                +""
                attrs {
                    onClickFunction = {
                        props.onShowAnswerClick()
                    }
                }
            }
            styledButton {
                css {
                    classes = mutableListOf("btn btn-success")
                    minWidth = 220.px
                }
                +""
                attrs {
                    onClickFunction = {
                        props.onNextClick()
                    }
                }
            }
        }
    }
}

fun RBuilder.navigation(handler: NavigationProps.() -> Unit): ReactElement {
    return child(Navigation::class) {
        this.attrs(handler)
    }
}
