import kotlinx.css.fontSize
import kotlinx.css.paddingBottom
import kotlinx.css.paddingLeft
import kotlinx.css.paddingTop
import kotlinx.css.px
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv
import styled.styledLabel

external interface KuralEluthuProps: RProps {
    var selectedThirukkural: Thirukkural
    var showAnswer: Boolean
    var firstLineWordsSrc: List<List<String>>
    var secondLineWordsSrc: List<List<String>>
    var firstLineWordsTarget: List<MutableList<String>>
    var secondLineWordsTarget: List<MutableList<String>>
    var elethukkal: List<Map<String, Boolean>>
    var noOfAttempts: Int
    var onLetterClick: (String) -> Unit
}

class KuralEluthu : RComponent<KuralEluthuProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = when (props.noOfAttempts) {
                    in 0..5 -> mutableListOf("alert alert-success")
                    in 6..9 -> mutableListOf("alert alert-warning")
                    else -> mutableListOf("alert alert-danger")
                }
            }
            +"No of attempts ${props.noOfAttempts}"
        }
        styledDiv {
            props.firstLineWordsTarget.forEach { word ->
                styledDiv {
                    css {
                        classes = mutableListOf("btn-group mr-3")
                    }
                }
                word.forEach { letter ->
                    styledButton {
                        css {
                            classes = getResultColor(letter == "_")
                        }
                        +letter
                    }
                }
            }
        }
        styledDiv {
            css {
                paddingTop = 10.px
            }
            props.secondLineWordsTarget.forEach { word ->
                styledDiv {
                    css {
                        classes = mutableListOf("btn-group mr-3")
                    }
                }
                word.forEach { letter ->
                    styledButton {
                        css {
                            classes = getResultColor(letter == "_")
                        }
                        +letter
                    }
                }
            }
        }
        styledDiv {
            css {
                paddingTop = 10.px
                paddingLeft = 10.px
            }
            props.elethukkal.forEach { line ->
                styledDiv {
                    css {
                        paddingBottom = 10.px
                    }
                    line.forEach { letter ->
                        styledButton {
                            css {
                                classes = getButtonStyle(letter.value)
                            }
                            +letter.key
                            attrs {
                                onClickFunction = {

                                    props.onLetterClick(letter.key)
                                }
                            }
                        }
                        styledLabel {
                            css {
                                fontSize = 50.px
                            }
                            +" "
                        }
                    }
                }
            }
        }
    }

    private fun getResultColor(hide: Boolean): MutableList<String> {
        val style = if (hide) "-outline" else ""
        return mutableListOf("btn btn$style-success mr-1")
    }

    private fun getButtonStyle(show: Boolean): MutableList<String> {
        val enabled = if (show) "" else "disabled"
        return mutableListOf("btn btn-primary mr-1 $enabled")
    }
}

fun RBuilder.kuralEluthu(handler: KuralEluthuProps.() -> Unit): ReactElement {
    return child(KuralEluthu::class) {
        this.attrs(handler)
    }
}
