import kotlinx.css.minWidth
import kotlinx.css.px
import kotlinx.html.id
import kotlinx.html.js.onClickFunction
import kotlinx.html.role
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv

external interface PersonProps: RProps {
    var thirukkurals: List<Thirukkural>
    var athikaram: AthikaramState
    var thirukkuralState: ThirukkuralState
    var wordState: WordState
    var filteredKuralState: FilteredKuralState
    var persons: List<String>
    var showAnswer: Boolean
    var selectedTopic: Topic
    var selectedPerson: String
    var noOfAttempts: Int
    var firstLineWordsSrc: List<List<String>>
    var secondLineWordsSrc: List<List<String>>
    var firstLineWordsTarget: List<MutableList<String>>
    var secondLineWordsTarget: List<MutableList<String>>
    var elethukkal: List<Map<String, Boolean>>
    var onShowAnswerClick: (Boolean) -> Unit
    var onPersonClick: (String) -> Unit
    var onTopicClick: (Topic) -> Unit
    var onNextClick: () -> Unit
    var onLetterClick: (String) -> Unit
    var onFilterClick: (Int) -> Unit
}

class Person : RComponent<PersonProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("d-flex flex-wrap justify-content-around")
            }
            styledDiv {
                css {
                    classes = mutableListOf("dropdown")
                }
                styledButton {
                    css {
                        classes = mutableListOf("btn btn-primary dropdown-toggle")
                        minWidth = 150.px
                    }
                    attrs {
                        id = "personDropDown"
                        role = "button"
                        attributes["data-toggle"] = "dropdown"
                        attributes["aria-haspopup"] = "true"
                        attributes["aria-expanded"] = "false"
                    }
                    +props.selectedPerson
                }
                styledDiv {
                    css {
                        classes = mutableListOf("dropdown-menu")
                    }
                    attrs {
                        attributes["aria-labelledby"] = "personDropDown"
                    }
                    props.persons.forEach { personName ->
                        styledButton {
                            css {
                                classes = mutableListOf("dropdown-item")
                            }
                            +personName
                            attrs {
                                onClickFunction = {
                                    props.onPersonClick(personName)
                                }
                            }
                        }
                    }
                }
            }
            styledDiv {
                css {
                    classes = mutableListOf("dropdown")
                }
                styledButton {
                    css {
                        classes = mutableListOf("btn btn-primary dropdown-toggle")
                        minWidth = 220.px
                    }
                    attrs {
                        id = "topicDropDown"
                        role = "button"
                        attributes["data-toggle"] = "dropdown"
                        attributes["aria-haspopup"] = "true"
                        attributes["aria-expanded"] = "false"
                    }
                    +props.selectedTopic.tamil
                }
                styledDiv {
                    css {
                        classes = mutableListOf("dropdown-menu dropdown-menu-right")
                    }
                    attrs {
                        attributes["aria-labelledby"] = "topicDropDown"
                    }
                    Topic.values().forEach { topic ->
                        if (topic == Topic.KuralEluthu) {
                            styledDiv {
                                css {
                                    classes = mutableListOf("dropdown-divider")
                                }
                            }
                        }
                        styledButton {
                            css {
                                classes = mutableListOf("dropdown-item")
                            }
                            +topic.tamil
                            attrs {
                                onClickFunction = {
                                    props.onTopicClick(topic)
                                }
                            }
                        }
                        if (topic == Topic.KuralEluthu) {
                            styledDiv {
                                css {
                                    classes = mutableListOf("dropdown-divider")
                                }
                            }
                        }
                    }
                }
            }
        }
        topicContent {
            selectedTopic = props.selectedTopic
            allThirukkurals = props.thirukkurals
            filteredKuralState = props.filteredKuralState
            thirukkuralState = props.thirukkuralState
            athikaram = props.athikaram
            wordState = props.wordState
            firstLineWordsSrc = props.firstLineWordsSrc
            firstLineWordsTarget = props.firstLineWordsTarget
            secondLineWordsSrc = props.secondLineWordsSrc
            secondLineWordsTarget = props.secondLineWordsTarget
            elethukkal = props.elethukkal
            showAnswer = props.showAnswer
            noOfAttempts = props.noOfAttempts
            onShowAnswerClick = { show -> props.onShowAnswerClick(show) }
            onNextClick = { props.onNextClick() }
            onLetterClick = { letter -> props.onLetterClick(letter) }
            onFilterClick = { props.onFilterClick(it) }
        }
    }
}

fun RBuilder.person(handler: PersonProps.() -> Unit): ReactElement {
    return child(Person::class) {
        this.attrs(handler)
    }
}
