import kotlinx.browser.window
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.await
import kotlinx.coroutines.launch
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.setState
import styled.css
import styled.styledDiv
import kotlin.random.Random

suspend fun fetchSource(): List<Thirukkural> {
    val sourceUrl = "https://raw.githubusercontent.com/dreamuth/dreamuth.github.io/master/kurals.txt"
    val sourceData = window.fetch(sourceUrl).await().text().await()
    val thirukkurals = readSource(sourceData)
    println("Source: $sourceUrl loaded")
    return thirukkurals
}

external interface AppState: RState {
    var loaded: Boolean
    var selectedTopic: Topic
    var selectedPerson: String
    var thirukkurals: List<Thirukkural>
    var athikaramState: AthikaramState
    var thirukkuralState: ThirukkuralState
    var wordState: WordState
    var filteredKuralState: FilteredKuralState
    var persons: List<String>
    var firstLineWordsSrc: List<List<String>>
    var secondLineWordsSrc: List<List<String>>
    var firstLineWordsTarget: List<MutableList<String>>
    var secondLineWordsTarget: List<MutableList<String>>
    var elethukkal: List<MutableMap<String, Boolean>>
    var noOfAttempts: Int
    var showAnswer: Boolean
}

class App : RComponent<RProps, AppState>() {
    override fun AppState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            val result = fetchSource()
            setState {
                thirukkurals = result
                selectedTopic = Topic.Athikaram
                athikaramState = AthikaramState(thirukkurals)
                thirukkuralState = ThirukkuralState(thirukkurals)
                filteredKuralState = FilteredKuralState(thirukkurals)
                wordState = WordState(thirukkurals)
                persons = listOf("") + thirukkurals.map { it.person }.distinct()
                selectedPerson = persons[0]
                initializeBlanksInKuralEluthu()
                initializeButtonsInKuralEluthu(true)
                noOfAttempts = 0
                loaded = true
                println(athikaramState.athikarams)
                println("No of athikarams: ${athikaramState.athikarams.size}")
                println("No of kurals: ${thirukkurals.size}")
                println("No of unique words from all kurals: ${wordState.words.size}")
            }
        }
    }

    private fun AppState.initializeBlanksInKuralEluthu() {
        firstLineWordsSrc = thirukkuralState.getCurrent().words.take(4).map { getCharList(it).toList() }
        secondLineWordsSrc = thirukkuralState.getCurrent().words.takeLast(3).map { getCharList(it).toList() }
        firstLineWordsTarget = thirukkuralState.getCurrent().words.take(4).map { MutableList(getCharList(it).size) { "_" } }
        secondLineWordsTarget = thirukkuralState.getCurrent().words.takeLast(3).map { MutableList(getCharList(it).size) { "_" } }
    }

    private fun AppState.initializeButtonsInKuralEluthu(state: Boolean) {
        val uyirLettersMap = uyirLetters.map { it to state }.toMap().toMutableMap()
        val meiLettersMap = meiLetters.map { it to state }.toMap().toMutableMap()
        elethukkal = listOf(uyirLettersMap, meiLettersMap)
    }

    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("alert alert-primary container-fluid text-center")
            }
            +" "
        }
        if (state.loaded) {
            person {
                thirukkurals = state.thirukkurals
                athikaram = state.athikaramState
                thirukkuralState = state.thirukkuralState
                wordState = state.wordState
                persons = state.persons
                showAnswer = state.showAnswer
                selectedTopic = state.selectedTopic
                selectedPerson = state.selectedPerson
                filteredKuralState = state.filteredKuralState
                firstLineWordsSrc = state.firstLineWordsSrc
                firstLineWordsTarget = state.firstLineWordsTarget
                secondLineWordsSrc = state.secondLineWordsSrc
                secondLineWordsTarget = state.secondLineWordsTarget
                elethukkal = state.elethukkal
                noOfAttempts = state.noOfAttempts
                onShowAnswerClick = { show ->
                    setState {
                        showAnswer = show
                        if (selectedTopic == Topic.KuralEluthu) {
                            firstLineWordsTarget = firstLineWordsSrc.map { it.toMutableList() }
                            secondLineWordsTarget = secondLineWordsSrc.map { it.toMutableList() }
                            initializeButtonsInKuralEluthu(false)
                        }
                    }
                }
                onPersonClick = { personName ->
                    setState {
                        if (state.selectedPerson != personName) {
                            showAnswer = false
                            selectedPerson = personName
                            thirukkuralState = if (selectedPerson == "") {
                                ThirukkuralState(thirukkurals)
                            } else {
                                ThirukkuralState(thirukkurals.filter { it.person == personName })
                            }
                            athikaramState = AthikaramState(thirukkuralState.kurals)
                            wordState = WordState(thirukkuralState.kurals)
                            filteredKuralState = FilteredKuralState(thirukkuralState.kurals)
                            initializeBlanksInKuralEluthu()
                            initializeButtonsInKuralEluthu(true)
                            noOfAttempts = 0
                        }
                    }
                }
                onTopicClick = { topic ->
                    setState {
                        if (selectedTopic != topic) {
                            showAnswer = false
                            selectedTopic = topic
                        }
                    }
                }
                onNextClick = {
                    when (selectedTopic) {
                        Topic.Athikaram, Topic.AthikaramPorul -> {
                            setState {
                                athikaramState = athikaramState.getNext()
                            }
                        }
                        Topic.KuralPorul -> {
                            setState {
                                thirukkuralState = thirukkuralState.getNext()
                            }
                        }
                        Topic.KuralSol -> {
                            setState {
                                wordState = wordState.getNext()
                            }
                        }
                        Topic.KuralEluthu -> {
                            setState {
                                thirukkuralState = thirukkuralState.getNext()
                                initializeBlanksInKuralEluthu()
                                initializeButtonsInKuralEluthu(true)
                                noOfAttempts = 0
                            }
                        }
                        Topic.Statistics -> {
                        }
                        Topic.AllKurals -> {
                        }
                    }
                }
                onLetterClick = { letter ->
                    checkLetter(letter, state.firstLineWordsSrc, state.firstLineWordsTarget)
                    checkLetter(letter, state.secondLineWordsSrc, state.secondLineWordsTarget)
                    setState {
                        noOfAttempts++
                        elethukkal.forEach { row ->
                            row.entries.forEach { entry ->
                                if (entry.key == letter) {
                                    entry.setValue(false)
                                }
                            }
                        }
                    }
                }
                onFilterClick = { size ->
                    setState {
                        filteredKuralState = if (size == 0) {
                            FilteredKuralState(thirukkuralState.kurals)
                        } else {
                            val filteredKurals = thirukkuralState.kurals.filter { getCharList(it.words[0]).size == size }
                            val filterIndex = getWordSize(thirukkuralState.kurals).indexOf(size) + 1
                            filteredKuralState.copy(index = filterIndex, kurals = filteredKurals)
                        }
                    }
                }
            }
        }
    }
    private fun checkLetter(letter: String, sourceList: List<List<String>>, targetLine: List<MutableList<String>>) {
        for (i in sourceList.indices) {
//            println("i = $i")
//            println(sourceList[i])
            val wordList = sourceList[i]
            for (j in wordList.indices) {
//                println("j = $j")
//                println(wordList[j])
                val keyLetter = getKeyLetter(wordList[j])
                if (letter == keyLetter) {
//                    println("Letter found $letter")
                    setState {
                        targetLine[i][j] = wordList[j]
                    }
                }
            }
        }
    }

    private fun getKeyLetter(char: String) =
        keyLetters[char] ?: error("Missing $char ${char.toCharArray()[0].toInt()}")
}

fun nextIndex(currentIndex: Int, maxIndex: Int): Int {
    var newIndex: Int
    do {
        newIndex = Random.nextInt(maxIndex)
    } while (newIndex == currentIndex)
    println("Current: $currentIndex to New: $newIndex of Total: $maxIndex")
    return newIndex
}

fun getWords(line: String): List<String> {
    val list = mutableListOf<String>()
    var currentWord = ""
    val charArray = line.toCharArray()
    for (char in charArray) {
//        println("Reading $char ${char.toInt()}")
        if (char.toInt() == 44) {
            println(line)
        }
        when (char.toInt()) {
            // Space, Dot, Zero-width non-joiner
            32, 39, 44, 46, 58, 8204 -> {
                if (currentWord.isNotBlank()) {
                    list.add(currentWord)
                }
                currentWord = ""
            }
            else -> {
                currentWord += char
            }
        }
    }
    return list
}
