enum class Topic(val tamil: String) {
    Athikaram(""),
    KuralSol(""),
    KuralPorul(""),
    AthikaramPorul(" "),
    KuralEluthu(" "),
    Statistics(" "),
    AllKurals("")
}

data class ThirukkuralState(val index: Int, val kurals: List<Thirukkural>) {
    constructor(thirukkurals: List<Thirukkural>): this(nextIndex(0, thirukkurals.size), thirukkurals)
    fun getCurrent(): Thirukkural = kurals[index]
    fun getNext(): ThirukkuralState = this.copy(index = nextIndex(index, kurals.size))
}

data class AthikaramState(val index: Int, val athikarams: List<String>) {
    constructor(thirukkurals: List<Thirukkural>): this(thirukkurals.map { it.athikaram }.distinct())
    constructor(athikarams: List<String>) : this(nextIndex(0, athikarams.size), athikarams)
    fun getCurrent(): String = athikarams[index]
    fun getNext(): AthikaramState = this.copy(index = nextIndex(index, athikarams.size))
}

data class WordState(val index: Int, val words: List<String>) {
    constructor(thirukkurals: List<Thirukkural>): this(thirukkurals.map { it.words }.flatten().distinct())
    constructor(words: List<String>) : this(nextIndex(0, words.size), words)
    fun getCurrent(): String = words[index]
    fun getNext(): WordState = this.copy(index = nextIndex(index, words.size))
}

data class FilteredKuralState(val index: Int, val kurals: List<Thirukkural>, val wordSizes: List<Int>) {
    constructor(thirukkurals: List<Thirukkural>): this(0, thirukkurals, getWordSize(thirukkurals))
}

fun getWordSize(thirukkurals: List<Thirukkural>): List<Int> {
    return thirukkurals.map { getCharList(it.words[0]).size }.toList().distinct().sorted()
}
