import kotlinx.css.minWidth
import kotlinx.css.paddingBottom
import kotlinx.css.paddingTop
import kotlinx.css.px
import kotlinx.html.id
import kotlinx.html.js.onClickFunction
import kotlinx.html.role
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv
import styled.styledSpan

external interface AllKuralsProps: RProps {
    var filteredKuralState: FilteredKuralState
    var onFilterClick: (Int) -> Unit
}

class AllKurals : RComponent<AllKuralsProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("dropdown d-flex flex-wrap justify-content-around")
                paddingBottom = 10.px
                paddingTop = 10.px
            }
            styledButton {
                css {
                    classes = mutableListOf("btn btn-primary dropdown-toggle")
                    minWidth = 151.px
                }
                attrs {
                    id = "allKuralsDropDown"
                    role = "button"
                    attributes["data-toggle"] = "dropdown"
                    attributes["aria-haspopup"] = "true"
                    attributes["aria-expanded"] = "false"
                }
                when (props.filteredKuralState.index) {
                    0 -> +"  "
                    else -> +"${props.filteredKuralState.wordSizes[props.filteredKuralState.index - 1]}    "
                }
                styledSpan {
                    css {
                        classes = mutableListOf("badge badge-light")
                    }
                    +"${props.filteredKuralState.kurals.size}"
                }
            }
            styledDiv {
                css {
                    classes = mutableListOf("dropdown-menu")
                }
                attrs {
                    attributes["aria-labelledby"] = "allKuralsDropDown"
                }
                styledButton {
                    css {
                        classes = mutableListOf("dropdown-item")
                    }
                    +" "
                    attrs {
                        onClickFunction = {
                            props.onFilterClick(0)
                        }
                    }
                }
                props.filteredKuralState.wordSizes.forEach { size ->
                    styledButton {
                        css {
                            classes = mutableListOf("dropdown-item")
                        }
                        +"$size    "
                        attrs {
                            onClickFunction = {
                                props.onFilterClick(size)
                            }
                        }
                    }
                }
            }
        }
        props.filteredKuralState.kurals.forEach { thirukkural ->
            kuralDisplay {
                selectedThirukkural = thirukkural
            }
        }
    }
}

fun RBuilder.allKurals(handler: AllKuralsProps.() -> Unit): ReactElement {
    return child(AllKurals::class) {
        this.attrs(handler)
    }
}
